h1,
h2,
h3,
h4 {
  color: #373737;
}

h1 {
  font-size: 2em;
  font-weight: 700;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 30px 0;
}

.designParagraph {
  line-height: 1.5em;
  margin: 0.5em 3em;
  font-size: 1em;
  font-weight: 500;
}

.blogPost {
  width: 80%;
  max-width: 1100px;
  background: white;
  margin: 20px auto 0;
  background: linear-gradient(white, white) padding-box,
    linear-gradient(to right, #244ae3, #04d7de) border-box;
  border-radius: 30px;
  border: 4px solid transparent;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.05);
}
.introParagraph {
  width: 90%;
  max-width: 1100px;

  margin: 20px auto 0;
  margin-top: 10%;
}
.blog-hero {
  width: 100%;
  height: 0;
  padding-bottom: 30%;
  background: url("https://images.unsplash.com/photo-1450101215322-bf5cd27642fc?crop=entropy&fit=crop&fm=jpg&h=1000&ixjsv=2.1.0&ixlib=rb-0.3.5&q=80&w=1925")
    20% 70% no-repeat;
  background-size: cover;
  position: relative;
}

summary {
  margin-top: 20px;
  display: inline-block;
}

summary h3 {
  width: 100%;
  font-size: 2.5em;
  color: #555;
  font-weight: 500;
}

summary .blogDate {
  width: 100%;
  font-size: 15px;
  padding-left: 2px;
  color: #909090;
}

article {
  padding-bottom: 50px;
}

.blogContent {
  width: 90%;
  margin: auto;
}

.blogImgDesign { border: black 2px solid;
  width: 100%;
  height: auto;
  border-radius: none;
  background-size: cover;
  position: relative;
}

.blogImgDesignCaption {
  margin-top: 0.5em;
  font-size: 0.9em;
  color: #888;
  text-align: center;
}

.image-gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 1em;
}

.image-gallery figure {
  flex: 1 1 calc(50% - 1em);
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width: 700px) {
  .image-gallery {
    flex-direction: column;
  }

  .image-gallery figure {
    flex: 1 1 100%;
  }
}

.referrences li {
  list-style-type: disc;
  margin-bottom: 10px;
  margin-top: 10px;
}

.biggerHeading {
  font-size: 4vw;
  color: black;
  padding: 20px;
  font-weight: bold;
  text-align: center;
}

.littleHeadingDesign {
  font-size: 2vw;
  font-family: cursive;
  padding: 20px;
  font-weight: bold;
}

@media (min-width: 601px) and (max-width: 1000px) {
  .blogPost {
    width: 90%;
  }
  .blogContent {
    width: 75%;
  }
  .blog-hero {
    padding-bottom: 35%;
  }
}

@media (max-width: 600px) {
  .blogPost {
    font-size: 0.9em;
    width: 90%;
  }
  .blogContent {
    width: 80%;
  }
  .blogImgDesign {
    width: 90%;
    padding-bottom: 5%;
  }
  .blog-hero {
    padding-bottom: 60%;
  }.biggerHeading {
    font-size: 40px;
  
  }
  .littleHeadingDesign {
    font-size: 20px;
    
  }
}
